@import url(https://fonts.googleapis.com/css?family=Exo:100,200,400);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:700,400,300);
body{
	margin: 0;
	padding: 0;
	background: #fff;

	color: #fff;
	font-family: Arial;
	font-size: 12px;
}

.body{
	position: absolute;
	top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
	width: auto;
	height: auto;
	background-image: url(/static/media/background.cdee8117.jpg);
	background-size: cover;
	z-index: 0;
}

.grad{
	position: absolute;
	top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
	width: auto;
	height: auto;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0)), color-stop(100%,rgba(0,0,0,0.65))); /* Chrome,Safari4+ */
	z-index: 1;
	opacity: 0.7;
}

.header{
	position: absolute;
    top: calc(42% - 35px);
    left: calc(38% - 255px);
    z-index: 2;
}

.header div{
	float: left;
    color: #fff;
    font-family: cursive;
    font-size: 80px;
    font-weight: 300;
}

.header div span{
	color: #5379fa !important;
}

.login{
	position: absolute;
    top: calc(45% - 75px);
    left: calc(50% - 50px);
    height: 200px;
    width: 450px;
    padding: 10px;
    z-index: 2;
}

.login input[type=text]{
	width: 450px;
    height: 50px;
    background: transparent;
    border: 1px solid rgba(255,255,255,0.6);
    border-radius: 2px;
    color: #fff;
    font-family: 'Exo', sans-serif;
    font-size: 26px;
    font-weight: 500;
    padding: 4px;
}

.login input[type=password]{
	width: 250px;
	height: 30px;
	background: transparent;
	border: 1px solid rgba(255,255,255,0.6);
	border-radius: 2px;
	color: #fff;
	font-family: 'Exo', sans-serif;
	font-size: 16px;
	font-weight: 400;
	padding: 4px;
	margin-top: 10px;
}

.login input[type=button]{
	width: 460px;
    height: 50px;
    background: #fff;
    border: 1px solid #fff;
    cursor: pointer;
    border-radius: 5px;
    color: #020104;
    font-family: 'Exo', sans-serif;
    font-size: 20px;
    font-weight: 400;
    padding: 6px;
    margin-top: 10px;
}

.login input[type=button]:hover{
	opacity: 0.8;
}

.login input[type=button]:active{
	opacity: 0.6;
}

.login input[type=text]:focus{
	outline: none;
	border: 1px solid rgba(255,255,255,0.9);
}

.login input[type=password]:focus{
	outline: none;
	border: 1px solid rgba(255,255,255,0.9);
}

.login input[type=button]:focus{
	outline: none;
}

::-webkit-input-placeholder{
   color: rgba(255,255,255,0.6);
}

::-moz-input-placeholder{
   color: rgba(255,255,255,0.6);
}
.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 40px;
  padding-left: 40px;
  border-left-style: inset;
  color: white;
  height: 90%;
  justify-content: space-between;
}

h3
{
  font-size: 30px;
}

.activeContainer {
  display: flex;
    align-items: center;
    margin-bottom: 50%;
    border-top-style: double;
    margin-top: 15px;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-right: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}
.messageBox {
  background: #F3F3F3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 100%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
    align-items: center;
    font-family: Helvetica;
    border-radius: 20px;
    color: #828282;
    border-color: #e0a7a7;
    border-style: dashed;
    padding: 5px;
    margin-right: 5px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: #2979FF;
}

.backgroundLight {
  background: #F3F3F3;
}
.messages {
