.outerContainer {
  	background-image: url(/static/media/background.cdee8117.jpg);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1A1A1D;
  }
  
  .container {
    margin: 0px 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 90%;
    width: 60%;
